const _hmt = window._hmt;

document.addEventListener("click", ({ target }) => {
  try {
    switch (target.tagName) {
      case "A":
      case "BUTTON":
        _hmt.push(["_trackEvent", target.innerText, ""]);
        break;
      case "INPUT":
        if (target.type === "radio") {
          _hmt.push(["_trackEvent", target.value, ""]);
        }
        break;
      case "DIV":
        if (target.classList.contains("ant-tabs-tab")) {
          _hmt.push(["_trackEvent", target.innerText, ""]);
        }
        break;
      case "IMG":
        _hmt.push(["_trackEvent", target.getAttribute("alt"), ""]);
        break;
      default:
        break;
    }
  } catch (e) {
    console.error(e);
  }
});



